import { firstValueFrom, isObservable, Observable } from 'rxjs';
import { ApiRestHeaders } from '../models/headers';
import { isPromise } from '../type-guards/is-promise';

/**
 * Handles the request modifications and provides a promise with all values
 */
export async function extractRequestModifications(
  requestModifications:
    | Observable<{
        url?: string;
        headers?: ApiRestHeaders;
        content?: string;
      }>
    | Promise<{
        url?: string;
        headers?: ApiRestHeaders;
        content?: string;
      }>
    | {
        url?: string;
        headers?: ApiRestHeaders;
        content?: string;
      }
): Promise<{
  updatedUrl: string;
  updatedHeaders: ApiRestHeaders;
  updatedContent: string;
}> {
  let updatedUrl: string;
  let updatedHeaders: ApiRestHeaders;
  let updatedContent: string;

  //Handle Observable
  if (isObservable(requestModifications)) {
    const modified = await firstValueFrom(requestModifications);
    updatedUrl = modified.url;
    updatedHeaders = modified.headers;
    updatedContent = modified.content;
  }
  // Handle Promise
  else if (isPromise(requestModifications)) {
    const modified = await requestModifications;
    updatedUrl = modified.url;
    updatedHeaders = modified.headers;
    updatedContent = modified.content;
  }
  // Handle Object
  else {
    updatedUrl = requestModifications.url;
    updatedHeaders = requestModifications.headers;
    updatedContent = requestModifications.content;
  }

  return { updatedUrl, updatedContent, updatedHeaders };
}
