import {
  DigitalApiRestHttpClient,
  DigitalApiRestHttpResponse,
  extractRequestModifications,
  ManipulateRequest
} from '@navitaire-digital/clients-core';
import { EnvironmentHealth } from '../types/types';

export class HealthClient {
  protected client: DigitalApiRestHttpClient;
  protected baseUrl: string;
  protected manipulateRequestService: ManipulateRequest;

  constructor(
    baseUrl: string,
    client: DigitalApiRestHttpClient,
    _manipulateRequestService?: ManipulateRequest
  ) {
    this.client = client;
    this.baseUrl = baseUrl;

    if (_manipulateRequestService) {
      this.manipulateRequestService = _manipulateRequestService;
    }
  }

  /**
   * Gets the current environment's health status.
   * @return The environment health model.
   */

  async v1_health_get(): Promise<
    DigitalApiRestHttpResponse<EnvironmentHealth>
  > {
    let url_ = this.baseUrl + '/api/v1/health';
    url_ = url_.replace(/[?&]$/, '');

    const headers_ = {
      Accept: 'application/json'
    };

    const updatedRequest = this.manipulateRequestService.manipulateRequest(
      url_,
      'GET',
      headers_
    );

    const { updatedUrl, updatedHeaders, updatedContent } =
      await extractRequestModifications(updatedRequest);

    return this.client.get<EnvironmentHealth>(
      updatedUrl || url_,
      updatedHeaders || headers_
    );
  }

  /**
   * Performs the dotREZ NSK API health check.
   */

  async nsk_v1_health_get(): Promise<
    DigitalApiRestHttpResponse<EnvironmentHealth>
  > {
    let url_ = this.baseUrl + '/api/nsk/v1/health';
    url_ = url_.replace(/[?&]$/, '');

    const headers_ = {
      Accept: 'application/json'
    };

    const updatedRequest = this.manipulateRequestService.manipulateRequest(
      url_,
      'GET',
      headers_
    );

    const { updatedUrl, updatedHeaders, updatedContent } =
      await extractRequestModifications(updatedRequest);

    return this.client.get<EnvironmentHealth>(
      updatedUrl || url_,
      updatedHeaders || headers_
    );
  }

  async tmp_v1_health_get(): Promise<
    DigitalApiRestHttpResponse<EnvironmentHealth>
  > {
    let url_ = this.baseUrl + '/api/tmp/v1/health';
    url_ = url_.replace(/[?&]$/, '');

    const headers_ = {
      Accept: 'application/json'
    };

    const updatedRequest = this.manipulateRequestService.manipulateRequest(
      url_,
      'GET',
      headers_
    );

    const { updatedUrl, updatedHeaders, updatedContent } =
      await extractRequestModifications(updatedRequest);

    return this.client.get<EnvironmentHealth>(
      updatedUrl || url_,
      updatedHeaders || headers_
    );
  }
}
