import {
  DigitalApiRestHttpClient,
  DigitalApiRestHttpResponse,
  extractRequestModifications,
  ManipulateRequest
} from '@navitaire-digital/clients-core';
import { GraphQlQuery, GraphQlQueryv2 } from '../types/types';

export class GraphClient {
  protected client: DigitalApiRestHttpClient;
  protected baseUrl: string;
  protected manipulateRequestService: ManipulateRequest;

  constructor(
    baseUrl: string,
    client: DigitalApiRestHttpClient,
    _manipulateRequestService?: ManipulateRequest
  ) {
    this.client = client;
    this.baseUrl = baseUrl;

    if (_manipulateRequestService) {
      this.manipulateRequestService = _manipulateRequestService;
    }
  }

  /**
   * Invokes a graph query and returns the response.
   * @return The graph json response.
   * @deprecated
   */

  async v1_graph_post<T>(
    query: GraphQlQuery
  ): Promise<DigitalApiRestHttpResponse<T>> {
    let url_ = this.baseUrl + '/api/v1/graph';
    url_ = url_.replace(/[?&]$/, '');

    const headers_ = {
      'Content-Type': 'application/json',
      Accept: 'application/octet-stream'
    };

    const content_ = JSON.stringify(query);
    const updatedRequest = this.manipulateRequestService.manipulateRequest(
      url_,
      'POST',
      headers_,
      content_
    );

    const { updatedUrl, updatedHeaders, updatedContent } =
      await extractRequestModifications(updatedRequest);

    return this.client.post<T>(
      updatedUrl || url_,
      updatedContent || content_,
      updatedHeaders || headers_
    );
  }

  /**
   * Invokes a graph query and returns the response.
   * @param queryName The unique name used for reporting.
   * @param query Query details.
   * @return The graph json response.
   */

  async v2_graph_queryName_post<T>(
    queryName: string | null,
    query: GraphQlQueryv2
  ): Promise<DigitalApiRestHttpResponse<T>> {
    let url_ = this.baseUrl + '/api/v2/graph/{queryName}';
    if (queryName === undefined || queryName === null)
      throw new Error("The parameter 'queryName' must be defined.");
    url_ = url_.replace('{queryName}', encodeURIComponent('' + queryName));
    url_ = url_.replace(/[?&]$/, '');

    const headers_ = {
      'Content-Type': 'application/json',
      Accept: 'application/octet-stream'
    };

    const content_ = JSON.stringify(query);
    const updatedRequest = this.manipulateRequestService.manipulateRequest(
      url_,
      'POST',
      headers_,
      content_
    );

    const { updatedUrl, updatedHeaders, updatedContent } =
      await extractRequestModifications(updatedRequest);

    return this.client.post<T>(
      updatedUrl || url_,
      updatedContent || content_,
      updatedHeaders || headers_
    );
  }

  /**
   * Invokes a graph query configured in utilities.
   * @param queryName The query name matching your resource management configuration.
   * @param cachedResults (optional) True to return cached results.
   * @param variables The json matching your json variables in your graph query.
   * @return The graph json response.
   */

  async nsk_v1_graph_queryName_post<T>(
    queryName: string | null,
    cachedResults: boolean | undefined,
    variables: any
  ): Promise<DigitalApiRestHttpResponse<T>> {
    let url_ = this.baseUrl + '/api/nsk/v1/graph/{queryName}?';
    if (queryName === undefined || queryName === null)
      throw new Error("The parameter 'queryName' must be defined.");
    url_ = url_.replace('{queryName}', encodeURIComponent('' + queryName));
    if (cachedResults === null)
      throw new Error("The parameter 'cachedResults' cannot be null.");
    else if (cachedResults !== undefined)
      url_ += 'cachedResults=' + encodeURIComponent('' + cachedResults) + '&';
    url_ = url_.replace(/[?&]$/, '');

    const headers_ = {
      'Content-Type': 'application/json',
      Accept: 'application/octet-stream'
    };

    const content_ = JSON.stringify(variables);
    const updatedRequest = this.manipulateRequestService.manipulateRequest(
      url_,
      'POST',
      headers_,
      content_
    );

    const { updatedUrl, updatedHeaders, updatedContent } =
      await extractRequestModifications(updatedRequest);

    return this.client.post<T>(
      updatedUrl || url_,
      updatedContent || content_,
      updatedHeaders || headers_
    );
  }
}
