import {
  DigitalApiRestHttpClient,
  DigitalApiRestHttpResponse,
  extractRequestModifications,
  ManipulateRequest
} from '@navitaire-digital/clients-core';
import {
  AccountChangePasswordRequestv2,
  AccountForgotPasswordRequest,
  IJsonResponse
} from '../types/types';

export class AccountClient {
  protected client: DigitalApiRestHttpClient;
  protected baseUrl: string;
  protected manipulateRequestService: ManipulateRequest;

  constructor(
    baseUrl: string,
    client: DigitalApiRestHttpClient,
    _manipulateRequestService?: ManipulateRequest
  ) {
    this.client = client;
    this.baseUrl = baseUrl;

    if (_manipulateRequestService) {
      this.manipulateRequestService = _manipulateRequestService;
    }
  }

  /**
   * Changes the accounts password.
   * @param request The change password request.
   */

  async nsk_v2_account_password_change_post(
    request: AccountChangePasswordRequestv2
  ): Promise<DigitalApiRestHttpResponse<IJsonResponse>> {
    let url_ = this.baseUrl + '/api/nsk/v2/account/password/change';
    url_ = url_.replace(/[?&]$/, '');

    const headers_ = {
      'Content-Type': 'application/json',
      Accept: 'application/json'
    };

    const content_ = JSON.stringify(request);
    const updatedRequest = this.manipulateRequestService.manipulateRequest(
      url_,
      'POST',
      headers_,
      content_
    );

    const { updatedUrl, updatedHeaders, updatedContent } =
      await extractRequestModifications(updatedRequest);

    return this.client.post<IJsonResponse>(
      updatedUrl || url_,
      updatedContent || content_,
      updatedHeaders || headers_
    );
  }

  /**
   * Invokes the forgot password reset for a specific account.
   * @param request The forgot password request.
   * @return Ok.
   */

  async nsk_v1_account_password_reset_post(
    request: AccountForgotPasswordRequest
  ): Promise<DigitalApiRestHttpResponse<IJsonResponse>> {
    let url_ = this.baseUrl + '/api/nsk/v1/account/password/reset';
    url_ = url_.replace(/[?&]$/, '');

    const headers_ = {
      'Content-Type': 'application/json',
      Accept: 'application/json'
    };

    const content_ = JSON.stringify(request);
    const updatedRequest = this.manipulateRequestService.manipulateRequest(
      url_,
      'POST',
      headers_,
      content_
    );

    const { updatedUrl, updatedHeaders, updatedContent } =
      await extractRequestModifications(updatedRequest);

    return this.client.post<IJsonResponse>(
      updatedUrl || url_,
      updatedContent || content_,
      updatedHeaders || headers_
    );
  }
}
