/**
 * "In production the 'rawValue' node will not be returned. These are for troubleshooting development issues only.",
 */
export interface DigitalApiMessageResponse {
    "rawValue"?: string;
    "code"?: string;
    "debugMode"?: string;
    "type"?: string;
    "value"?: string;
    "status"?: MessageStatus;
    "details"?: any;
}

export enum MessageStatus {
    /**
     * The default value.
     */
    General = 0,

    /**
     * Action is required but not immediately.
     */
    Warning = 1,

    /**
     * Used when action is required on an urgent basis.
     */
    Critical = 2
}